@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Kosugi+Maru&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Kosugi+Maru&family=Noto+Sans+TC:wght@100..900&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+SC:wght@100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+SC:wght@100..900&family=Wix+Madefor+Display:wght@400..800&display=swap');

:root {
  --container-padding: 1.25rem;
  --breakpoint-sm: 428px;
  --breakpoint-md: 576px;
  --breakpoint-lg: 768px;
  --breakpoint-xl: 992px;
  --breakpoint-2xl: 1179px;
  --breakpoint-3xl: 1400px;
  --breakpoint-4xl: 1800px;
  --breakpoint-5xl: 1921px;
  --max-width-sm: 368px;
  --max-width-md: 540px;
  --max-width-lg: 728px;
  --max-width-xl: 960px;
  --max-width-2xl: 1114px;
  --max-width-3xl: 1352px;
  --max-width-4xl: 1704px;
  --max-width-5xl: 1792px;
}

body {
  font-family: 'Wix Madefor Display', 'Noto Sans TC', sans-serif;
  background-color: #F5F5F5;
}


.content-container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: var(--container-padding);
  padding-right: var(--container-padding);
}

@media (min-width: var(--breakpoint-sm)) {
  .content-container {
    max-width: var(--max-width-sm);
  }
}
@media (min-width: var(--breakpoint-md)) {
  .content-container {
    max-width: var(--max-width-md);
  }
}
@media (min-width: var(--breakpoint-lg)) {
  .content-container {
    max-width: var(--max-width-lg);
  }
}
@media (min-width: var(--breakpoint-xl)) {
  .content-container {
    max-width: var(--max-width-xl);
  }
}
@media (min-width: var(--breakpoint-2xl)) {
  .content-container {
    max-width: var(--max-width-2xl);
  }
}
@media (min-width: var(--breakpoint-3xl)) {
  .content-container {
    max-width: var(--max-width-3xl);
  }
}
@media (min-width: var(--breakpoint-4xl)) {
  .content-container {
    max-width: var(--max-width-4xl);
  }
}
@media (min-width: var(--breakpoint-5xl)) {
  .content-container {
    max-width: var(--max-width-5xl);
  }
}